import React, { useState } from 'react'
import UrlPage from './Components/UrlPage';
import Categories from './Components/Categories';

function App() {
  const [selectedCategory, setSelectedCategory] = useState("");
  const [folderList, setFolderList] = useState(JSON.parse(localStorage.getItem("folderList")) ? JSON.parse(localStorage.getItem("folderList")) : []);

  const [url, setUrl] = useState(false);
  return (
    <div className="App">
      {
        url ? <UrlPage selectedCategory={selectedCategory} folderList={folderList} setFolderList={setFolderList} setUrl={setUrl} setSelectedCategory={setSelectedCategory} /> : <Categories selectedCategory={selectedCategory} folderList={folderList} setFolderList={setFolderList} setUrl={setUrl} setSelectedCategory={setSelectedCategory} />
      }
    </div>
  );
}

export default App;
