import React, { useEffect, useState } from 'react';
import { Grid, Typography, TextField, Button, Link, Avatar } from '@material-ui/core'
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Divider from '@material-ui/core/Divider';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import ListSubheader from '@material-ui/core/ListSubheader';
import DeleteIcon from '@material-ui/icons/Delete';
import Logo from '../../Assets/logo.jpg';
import IconButton from '@material-ui/core/IconButton';
import ArrowLeftIcon from '@material-ui/icons/ArrowLeft';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import PrintIcon from '@material-ui/icons/Print';

const Index = ({ selectedCategory, setUrl, folderList, setFolderList }) => {

    const [urlData, setUrlData] = useState({});
    const [urlList, setUrlList] = useState(JSON.parse(localStorage.getItem("urlList")) ? JSON.parse(localStorage.getItem("urlList")) : []);
    const [open, setOpen] = React.useState(false);
    const [action, setAction] = useState("");

    const onTextChange = (e) => {
        setUrlData({
            ...urlData,
            [e.target.name]: e.target.value,
            selectedCategory: selectedCategory
        })
    }

    const onSubmit = () => {
        window.event.preventDefault();
        if (urlData.name && urlData.url) {

            setUrlList([
                ...urlList,
                urlData
            ])
        }
    }

    const setDialogAction = (str) => {
        setAction(str);
        setOpen(true);
    }

    const deleteItem = (index) => {
        if (index > -1) {
            let newArray = urlList.filter((val, i) => i !== index);
            setUrlList(newArray);
        }
    }

    const deleteFolder = () => {
        let newArray = folderList.filter((val, i) => val.name !== selectedCategory);
        console.log({ newArray })
        setFolderList(newArray);
        setUrl(false);
    }

    useEffect(() => {
        localStorage.setItem("urlList", JSON.stringify(urlList));
    }, [urlList])


    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <form >
            {
                open && <Dialog
                    open={open}
                    maxWidth="sm"
                    onClose={handleClose}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">{action === "folder" ? "Delete Folder" : "Delete Url"}</DialogTitle>
                    <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                            {action === "folder" ? "Are you sure you want to delete this folder ? By deleting this folder, all the urls will be deleted." : 'Are you sure you want to delete this url ?'}
                        </DialogContentText>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleClose} variant="outlined" color="primary">
                            No
                        </Button>
                        <Button onClick={deleteFolder} variant="contained" color="primary" autoFocus>
                            Yes
                        </Button>
                    </DialogActions>
                </Dialog>
            }
            <Grid container spacing={2} style={{ margin: 'auto', width: '90vw', marginTop: 20, padding: 15, boxShadow: "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)" }}>

                <Grid item container justify="space-between" alignItems="center" xs={12}>
                    <Avatar alt="Shop List Logo" src={Logo} style={{ width: 70, height: 70, marginBottom: 20, marginRight: 10 }} />
                    <Typography variant="h5" align="center" >{selectedCategory}</Typography>
                    <Button
                        variant="contained"
                        color="primary"
                        startIcon={<DeleteIcon />}
                        size="small"
                        onClick={() => setDialogAction("folder")}
                        style={{ marginLeft: 10 }}
                    >
                        Delete Folder
                    </Button>

                </Grid>
                <Grid item container justifyContent="center" xs={12}>
                    <Grid item container spacing={2} xs={12} lg={10}>
                        <Grid item xs={12} lg={4}>
                            <TextField
                                id="standard-full-width"
                                label="Name of Shop"
                                name="name"
                                style={{ margin: 6 }}
                                placeholder="Amazon, Aliexpress etc"
                                onChange={(e) => onTextChange(e)}
                                // helperText="Full width!"
                                fullWidth
                                required
                                margin="normal"
                                InputLabelProps={{
                                    shrink: true,
                                }}
                            />
                        </Grid>

                        <Grid item xs={12} lg={8}>
                            <TextField
                                id="standard-full-width"
                                label="Enter URL"
                                name="url"
                                required
                                style={{ margin: 6 }}
                                placeholder="https://www.aliexpress.com"
                                // helperText="Full width!"
                                fullWidth
                                margin="normal"
                                onChange={(e) => onTextChange(e)}
                                InputLabelProps={{
                                    shrink: true,
                                }}
                            />
                        </Grid>



                        <Grid container justifyContent="flex-end">
                            <Button variant="contained" type="submit" onClick={onSubmit} color="primary">
                                Submit
                            </Button>
                        </Grid>
                    </Grid>
                </Grid>

                <Grid item container justifyContent="center" xs={12}>
                    <Grid item container justifyContent="center" xs={12}>
                        <List component="nav" style={{ minWidth: '100%', }} aria-label="main mailbox folders" subheader={
                            <ListSubheader component="div" style={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center' }} id="nested-list-subheader">
                                <span>Saved Urls</span>
                                <IconButton >
                                    <PrintIcon />
                                </IconButton>
                            </ListSubheader>
                        }>

                            {
                                urlList.map((value, index) => value.selectedCategory === selectedCategory && (
                                    <div>
                                        <ListItem button key={index} component="a" rel="external" target="_blank" href={`//${value?.url}`}>
                                            <ListItemIcon>
                                                <AddShoppingCartIcon />
                                            </ListItemIcon>
                                            <ListItemText primary={value.url} secondary={value.name} />
                                            <ListItemSecondaryAction>
                                                <IconButton edge="end" aria-label="delete" onClick={() => deleteItem(index)}>
                                                    <DeleteIcon />
                                                </IconButton>
                                            </ListItemSecondaryAction>
                                        </ListItem>
                                        <Divider variant="middle" />
                                    </div>
                                ))
                            }
                        </List>
                    </Grid>
                </Grid>
                <Grid item >
                    <Button
                        variant="contained"
                        color="primary"
                        startIcon={<ArrowLeftIcon />}
                        size="small"
                        onClick={() => setUrl(false)}
                    >
                        Back
                    </Button>

                </Grid>
            </Grid>
        </form >
    )
}

export default Index;
